a{
    color: #222;
    text-decoration: none;
}
header{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 70px;
    box-shadow: 0 0 6px rgba(0, 0, 0, 0.3);
    z-index: 5;
    color: #FFF;
    border-bottom: 1px solid rgba(255,255,255,0.4);
}
header .logo{
    font-weight: 600;
    font-size: 30px;
    letter-spacing: 1px;
    color: #FFF;
}
header .container{
    height: 100%;
}
header .container .inner{
    height: 100%;
    padding: 15px 0;
    display: flex;
    align-items: center;
}
.home{
    min-width: 1400px;
}
.container{
    min-width: 1400px;
    max-width: 1400px;
    margin: 0 auto;
    padding: 0 20px;
    position: relative;
}
.App{
    position: relative;
} 
.visual{
    height: 460px;
    background: url('./../public/img/bg1.jpg') 50% 50% no-repeat;
    background-size: cover;
    position: relative;
    color: #FFF;
    padding-top: 50px;
}
.visual .container{
    position: relative;
    top: 50%;
    transform: translateY(-50%);
}
.visual h2.title{
    font-size: 33px;
    font-weight: 300;
    margin-bottom: 10px;
}
.visual h3{
    font-size: 40px;
    font-weight: 500;
    margin-bottom: 40px;
}
.visual p.exp{
    font-size: 18px;
    font-weight: 300;
}
.visual::before{
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0,0,0,0.3);
}
.keywords{
    padding: 80px 0;
    background: #fff;
}
.keywords .container{
    overflow: auto;
}
.keywords .tableWrap{
    width: 100%;
    border: 1px solid #e3e3e3;
    border-radius: 14px;
    overflow: hidden;
}
.keywords table{
    width: 100%;
}
.keywords table th{
    padding: 14px 12px;
    border-bottom: 2px solid #222;
    background: #f8f8f8;
    font-size: 20px;
    font-weight: 500;
    border-collapse: collapse;
    border-spacing: 0;
}
.keywords table td{
    border: 1px solid #e3e3e3;
    background: #FFF;
    text-align: center;
    padding: 12px;
    font-size: 18px;
    font-weight: 500;
    vertical-align: middle;
}
.keywords table td:first-child{
    border-left: 0;
}
.keywords table td:last-child{
    border-right: 0;
}
.keywords table td a{
    text-decoration: underline;
    text-underline-position: under;
}
.dateTr td{
    height: 0 !important;
    padding: 0 !important;
    border: 0 !important;
}
.dateTr+tr td{
    border-top: 1px solid #555 !important;
}

.chartWrap{
    padding: 20px 0;
}